.game {
    -webkit-transition: 0.2s;
    -o-transition: 0.2s;
    transition: 0.2s; 
}

.game:hover {
    cursor: pointer;
    -webkit-transform: scale(1.05);
        -ms-transform: scale(1.05);
            transform: scale(1.05);
    -webkit-transition: 0.2s;
    -o-transition: 0.2s;
    transition: 0.2s;
}

#cardImgOver {
    padding: 0;
}

.overlayBackground {
    width: 100%;
    height: 100%;
    padding: 0.5em;
    display: -ms-flexbox;
    display: flex;
    -ms-flex-direction: column;
        flex-direction: column;
    background: rgba(18, 18, 18);
    opacity: 0;
}

.overlayBackground:hover {
    background: rgba(10, 35, 60, 0.9);
    opacity: 1;
}

#selectButton {
    margin: auto;
}

#selectButton:hover {
    color: rgba(10, 35, 60, 0.9);
}


